<template>
  <div>
    <a-form>
      <a-alert :message="`设计ID：`+design.id" type="info" banner />
      <a-form-item label="标题" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
        <a-input v-model="design.title" />
      </a-form-item>
      <a-form-item label="技术简介" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
        <a-input v-model="design.subtitle" />
      </a-form-item>
      <a-form-item label="价格" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
        <a-input v-model="design.price" />
      </a-form-item>
      <a-form-item label="内容" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
        <a-textarea v-model="design.content" :rows="4" />
      </a-form-item>
      <a-form-item :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }" label="是否有文档">
        <a-checkbox v-model="design.document">是否有文档</a-checkbox>
      </a-form-item>
      <a-form-item label="设计语言" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
        <a-select
          v-model="design.language"
          showSearch
          placeholder="请选择设计语言"
          @change="handleSelectChange"
        >
          <a-select-option value="c#">c#</a-select-option>
          <a-select-option value="java">java</a-select-option>
          <a-select-option value="flutter">flutter</a-select-option>
          <a-select-option value="react native">react native</a-select-option>
          <a-select-option value="vue">vue</a-select-option>
          <a-select-option value="react">react</a-select-option>
          <a-select-option value="spring boot">spring boot</a-select-option>
          <a-select-option value="asp.net">asp.net</a-select-option>
          <a-select-option value="php">php</a-select-option>
          <a-select-option value="jsp">jsp</a-select-option>
          <a-select-option value="小程序">小程序</a-select-option>
          <a-select-option value="winform">winform</a-select-option>
          <a-select-option value="wpf">wpf</a-select-option>
          <a-select-option value="electron">electron</a-select-option>
          <a-select-option value="arduino">arduino</a-select-option>
          <a-select-option value="51">51</a-select-option>
          <a-select-option value="stm32">stm32</a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item label="类型" :label-col="{ span:5}" :wrapper-col="{ span: 12 }">
        <a-select v-model="design.type" placeholder="请选择设计类型" @change="handleSelectChange">
          <a-select-option value="app">app</a-select-option>
          <a-select-option value="desktop">desktop</a-select-option>
          <a-select-option value="web">web</a-select-option>
          <a-select-option value="iot">iot</a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item label="标签" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
        <a-select
          v-model="design.tags"
          mode="tags"
          :tokenSeparators="[',']"
          placeholder="请选择标签"
          @change="handleSelectChange"
        >
          <a-select-option value="c#">c#</a-select-option>
          <a-select-option value="java">java</a-select-option>
          <a-select-option value="flutter">flutter</a-select-option>
          <a-select-option value="react native">react native</a-select-option>
          <a-select-option value="vue">vue</a-select-option>
          <a-select-option value="react">react</a-select-option>
          <a-select-option value="spring boot">spring boot</a-select-option>
          <a-select-option value="asp.net">asp.net</a-select-option>
          <a-select-option value="php">php</a-select-option>
          <a-select-option value="jsp">jsp</a-select-option>
          <a-select-option value="小程序">小程序</a-select-option>
          <a-select-option value="winform">winform</a-select-option>
          <a-select-option value="wpf">wpf</a-select-option>
          <a-select-option value="electron">electron</a-select-option>
          <a-select-option value="arduino">arduino</a-select-option>
          <a-select-option value="51">51</a-select-option>
          <a-select-option value="stm32">stm32</a-select-option>
          <a-select-option value="mysql">mysql</a-select-option>
          <a-select-option value="sqlite">sqlite</a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item label="图片" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
        <!-- <a-textarea
        :value="imagesLine"
        placeholder="一行一个图片链接"
        :rows="4"
        />-->
        <a-collapse defaultActiveKey="1" :bordered="true">
          <a-collapse-panel header="查看" key="1">
            <a-list itemLayout="horizontal" :dataSource="design.images">
              <div slot="header">
                <a-input-search
                  placeholder="图片URL"
                  @search="imagesAdd"
                  enterButton="添加"
                  size="default"
                />
              </div>
              <a-list-item slot="renderItem" slot-scope="item, index">
                <a slot="actions">编辑</a>
                <a slot="actions" @click="imagesDelete(index)">删除</a>
                <a-list-item-meta>
                  <a slot="description">{{item}}</a>
                </a-list-item-meta>
              </a-list-item>
            </a-list>
          </a-collapse-panel>
        </a-collapse>
      </a-form-item>

      <a-form-item label="视频" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
        <a-collapse defaultActiveKey="1" :bordered="true">
          <a-collapse-panel header="查看" key="1">
            <a-list itemLayout="horizontal" :dataSource="design.videos">
              <div slot="header">
                <a-input-search
                  placeholder="视频URL"
                  @search="videosAdd"
                  enterButton="添加"
                  size="default"
                />
              </div>
              <a-list-item slot="renderItem" slot-scope="item, index">
                <a slot="actions">编辑</a>
                <a slot="actions" @click="videosDelete(index)">删除</a>
                <a-list-item-meta>
                  <a slot="description">{{item}}</a>
                </a-list-item-meta>
              </a-list-item>
            </a-list>
          </a-collapse-panel>
        </a-collapse>
      </a-form-item>
      <a-form-item :wrapper-col="{ span: 12, offset: 5 }">
        <a-button type="primary" @click="updateDesign">修改</a-button>
        <a-button type="danger" @click="deleteDesign" style="margin-left:15px">删除</a-button>
      </a-form-item>
    </a-form>
  </div>
</template>

<script>
import { ObjectID } from "bson";
export default {
  data() {
    return {
      formLayout: "horizontal",
      form: this.$form.createForm(this),
      design: {}
    };
  },
  methods: {
    handleSelectChange(value) {
      //   console.log(value);
      //   this.form.setFieldsValue({
      //     note: `Hi, ${value === "male" ? "man" : "lady"}!`
      //   });
    },
    updateDesign() {
      this.axios
        .put("design", this.design)
        .then(response => {
          if (response.data) {
            this.$message.success("修改设计成功!");
          }
        })
        .catch(response => {
          console.log(response);
        });
    },
    deleteDesign() {
      this.axios
        .delete("design/"+ this.design.id)
        .then(response => {
          if (response.data) {
            this.$message.success("删除设计成功!");
          }
        })
        .catch(response => {
          console.log(response);
        });
    },
    imagesDelete(index) {
      console.info(index);
      this.design.images.splice(index, 1);
    },
    videosDelete(index) {
      console.info(index);
      this.design.videos.splice(index, 1);
    },
    imagesAdd(value) {
      this.design.images.push(value);
    },
    videosAdd(value) {
      this.design.videos.push(value);
    }
  },
  mounted() {
    this.axios
      .get("design/" + this.$route.params.id)
      .then(response => {
        this.design = response.data;
      })
      .catch(response => {
        console.log(response);
      });
  }
};
</script>


